<template>
  <div>
    <div id="banner">
      <div class="headerSlider mall-today" v-if="todaySpike.length">
        <swiper :options="swiperOption" ref="mySwiperA" class="swiper-wrapper">
          <!--  -->
          <swiper-slide class="banner" v-for="item in todaySpike"> <!--v-for="item in bannerList"-->
            <a href="javascript:void(0)">
              <div class="swiper-img">
                <img src="../../../static/aaa.jpg" alt="">
              </div>
              <p>{{item.title}}</p>
              <p class="num" data-stocknum="item.stockNum"><span class="jifen_price">{{item.price}}</span><s class="market_price">{{item.originalPrice}}</s></p>
              <p class="mall-red"><span>{{item.showSales}}</span></p>
            </a>
          </swiper-slide>
          <!--<swiper-slide class="banner" > &lt;!&ndash;v-for="item in bannerList"&ndash;&gt;-->
            <!--<a href="javascript:void(0)">-->
              <!--<div class="swiper-img">-->
                <!--<img src="../../../static/aaa.jpg" alt="">-->
              <!--</div>-->
              <!--<p> 95gX7</p>-->
              <!--<p class="num" data-stocknum="500"><span class="jifen_price">55</span><s class="market_price">69</s></p>-->
              <!--<p class="mall-red"><span>5</span></p>-->
            <!--</a>-->
          <!--</swiper-slide>-->
        </swiper>
      </div>
    </div>

  </div>


</template>
<script>
  import Vue from 'vue'
  import VueAwesomeSwiper from 'vue-awesome-swiper';
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import {getMallList} from '../../api';

  Vue.use(VueAwesomeSwiper);

  export default{
    data() {
      return {
        todaySpike: [],
        swiperOption: {
          notNextTick: true,
          slidesPerView: 3,
//          spaceBetween: 30,
          freeMode: true,
          debugger: true
        }
      }
    },
    created(){
//      this.getBanner();
      this.getMallList();
    },
    mounted(){
    },
    methods: {
      getMallList(){
        getMallList({type:'Spike'}).then((res)=>{
            if(res.status == 200){
              this.todaySpike = res.data.data;
            }
        })
      }
    },
    components: {
      swiper,
      swiperSlide
    }
  }
</script>
<style scoped>
  .headerSlider {
    width: 100%;
    height: auto;
  }

  .headerSlider .swiper-wrapper {
    width: 100%;
    height: 100%;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
  }

  .mall-today .swiper-slide {
    border-left: 1px solid #f4f4f4;
    padding: 0.10rem 0.175rem;
    height: 6.75rem;
    background-color: #fff;
  }

  .mall-today .swiper-slide:last-child {
    border-right: 2px solid #f4f4f4;
  }

  .mall-today .swiper-slide .swiper-img, .mall-today .swiper-slide .swiper-img img {
    width: 5.25rem;
    height: 3.0rem;
    overflow: hidden;
    font-size: 0.55rem;
  }

  .swiper-img, .default_background {
    background: url(data:image/png;base64,) 0 0 no-repeat;
    -webkit-background-size: 100%;
    background-size: 100%;
  }

  .mall-today .swiper-slide a p {
    font-size: 0.555rem;
    line-height: 1rem;
    text-align: left;
  }

  .mall-today .swiper-slide p:nth-child(2) {
    color: #333;
  }

  .mall-today .swiper-slide p:nth-child(3) span {
    font-size: 0.65rem;
  }

  .mall-today .swiper-slide p:nth-child(2) {
    margin-top: 0.35rem;
    display: -webkit-box;
    display: box;
    overflow: hidden;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    height: 1rem;
  }

  .swiper-container-all {
    width: 100%;
    overflow: hidden;
    top: 0;
    left: 0;
    z-index: 9999;
    background: #fff;
  }

  .mall-all, .swiper-img {
    position: relative;
  }
  .mall-today .swiper-slide a p.num{ font-size:0.5rem; color:#fa4619;}
  .mall-today .swiper-slide a p .jifen_price{ font-size:0.7rem;}
  .goods-img h6 span .jifen_price{ font-size:0.7rem;}
  .mall-today .swiper-slide a p .market_price{ color:#666; margin-left:0.2rem;}
  .mall-red {color: #fa4619;}
</style>



