import axios from 'axios';
import MockAdapter from 'axios-mock-adapter';
import { homePage, todaySpike, goodsList } from './data';
let _homePage = homePage;

export default {
  /**
   * mock bootstrap
   */
  bootstrap() {
    let mock = new MockAdapter(axios);

    //
    mock.onGet('/homePage/api.do').reply(config => {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve([200, {
            data: _homePage
          }]);
        }, 1000);
      });
    });

    // 
    mock.onGet('http://api.jiuxiulvxing.com/web.api').reply(config => {
      let {type} = config.params;
      let result ;
      if(type == 'Spike'){
        result = todaySpike
      }else{
        result = goodsList
      }
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve([200, {
            data: result
          }]);
        }, 1000);
      });
    });
  }
};



