<template>
  <div>
    <div class="headerSlider mall-list-tab">
      <swiper :options="swiperOption" ref="mySwiperA" class="swiper-wrapper">
        <!--  -->
        <swiper-slide class="banner active"> <!--v-for="item in bannerList"-->
          <span></span><i></i>
        </swiper-slide>
        <swiper-slide class="banner">
          <span></span><i></i>
        </swiper-slide>
        <swiper-slide class="banner">
          <span></span><i></i>
        </swiper-slide>
        <swiper-slide class="banner">
          <span></span><i></i>
        </swiper-slide>
        <swiper-slide class="banner">
          <span></span><i></i>
        </swiper-slide>
      </swiper>
    </div>

    <div class="goods-ul-box">
        <div class="goods-ul-num">
          <ul class="mall-goods-wrapper mall-all-goods" v-if="goodsList.length">
            <li v-for="item in goodsList"  data-title="item.title">
              <div class="goods-img">
                <i v-if="item.stockNum == 0" class="left_goods"></i>
                <i class="host_icon"></i>
                <div class="default_background">
                  <img src="/static/img/aaa.aeb2729.jpg" alt=""></div>
                <h3>{{item.title}}</h3>

                <h4>
                  <span v-if="item.stockNum > 0" >
                    <span class="mall-red"></span><span class="jifen_price mall-red">{{item.price}}</span>
                  </span>
                  <span v-else="item.stockNum == 0">
                    <span></span><span class="jifen_price">{{item.price}}</span>
                  </span>
                  <s v-if="item.originalPrice > 0" class="market_price">{{item.originalPrice}}</s>
                </h4>
                <h6 class="color_gray">
                  <span v-if="item.stockNum > 0" class="mall-red">{{item.pointPrice}}</span>
                  <span  v-else >{{item.pointPrice}}</span>
                  <span>{{item.sales}}</span>
                </h6>
                <h5 v-if="item.stockNum == 0" class="mall_gray" data-num="item.stockNum">
                  
                </h5>
                <h5 v-else="item.stockNum > 0" data-num="item.stockNum"></h5>
              </div>
            </li>
          </ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
          <ul class="mall-goods-wrapper mall-all-goods" style="display: none;"></ul>
        </div>
      </div>
  </div>

</template>

<script>
  import Vue from 'vue'
  import VueAwesomeSwiper from 'vue-awesome-swiper';
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import {getMallList} from '../../api';

  Vue.use(VueAwesomeSwiper);

  export default{
    data() {
      return {
        goodsList: [],
        swiperOption: {
          notNextTick: true,
          slidesPerView: 4,
          freeMode: true,
          debugger: true
        }
      }
    },
    created(){
      this.getMallList();
    },
    mounted(){
    },
    methods: {
      getMallList(){
        getMallList({type:'GoodsList'}).then((res)=>{
          if(res.status == 200){
            this.goodsList = res.data.data;
          }
        })
      }
    },
    components: {
      swiper,
      swiperSlide
    }
  }
</script>
<style lang="scss" scoped>
  .headerSlider {
    width: 100%;
    height: auto;
  }

  .headerSlider .swiper-wrapper {
    width: 100%;
    height: 100%;
  }

  .mall-list-tab {
    width: 100%;
    height: 2.25rem;
    line-height: 2.2rem;
    margin: 0.25rem 0;
    background: #fff;

  .swiper-slide {
    width: 70px;
    text-align: center;
    font-size: 0.75rem;
    color: #666;
      i {
        width: 100%;
        height: 0.15rem;
        position: absolute;
        left: 0;
        bottom: 0;
        opacity: 0;
        background: #fb9b00;
      }
    }
    .active {
      color: #fb9b00;
      i {
        opacity: 1;
      }
    }
  }
  .goods-ul-box{margin-bottom: 2rem;}
  .mall-all-goods,.goods-img {padding: 0.25rem;}
  .mall-all { min-height: 25rem; }
  .goods-img { position: relative;text-align: left; }
  .default_background { height: 5.0rem; overflow: hidden;}
  .goods-img img { width: 100%; }

  .goods-img h3 { display: -webkit-box; display:box; overflow: hidden; -webkit-line-clamp: 2; -webkit-box-orient:vertical; height: 1.8rem; line-height: 0.9rem; }
  .goods-img h3 { font-size: 0.7rem; margin: 0 0.25rem;}
  .goods-img h4 { line-height: 1.2rem; margin: 0 0.25rem;}
  .goods-img h4 span:nth-child(1) {font-size: 0.6rem;}

  .goods-img h4 s { margin-left: .2rem; color: #333; font-size: 0.6rem;}
  .goods-img h4 span:nth-child(2) {font-size: 0.75rem;}
  .goods-img h4 span:nth-child(3) {font-size: 0.5rem; color:#999;text-decoration: line-through; margin-left: 0.5rem;}
  .goods-img h6 { font-size: 0.55rem; position: relative; width: 100%; min-height: 0.8rem;  }
  .goods-img h6 span { min-width: 1rem;  margin: 0 0.25rem;}
  .goods-img h6 span:nth-child(1) { position: absolute; left: 0; }
  .goods-img h6 span:nth-child(2) { position: absolute; right: 0; color: #999; }
  .goods-img h5 { font-size: 0.7rem; line-height: 1.375rem; width: 8.0rem; height: 1.375rem; background: #fa4619; color: #fff; text-align: center; border-radius: 0.125rem; margin: 0 0.25rem; }
  .mall-all-goods li {  background: #fff; min-height: 10rem; width: 9.0rem; margin-right: 0.25rem; float: left; margin-bottom: 0.25rem; border-radius: 0.1rem;}
  .mall-all-goods li:nth-child(2n) {margin-right: 0;}
  .mall-redeem,.mall-redeem-disable { position: absolute; bottom: 0.25rem; right: 0.25rem; width: 3.55rem; height: 1.05rem; border: 1px solid #fca28c; font-size: 0.6rem; text-align: center; line-height: 1.05rem;  border-radius: 0.15rem;box-shadow: 0px 2px 2px rgba(0,0,0,.1);}
  .mall-redeem-disable { border: 1px solid #ccc; color: #ccc; }
  .mall-more { border: 1px solid #e1e1e1; padding: 0.25rem 0.375rem; box-shadow: 0px 2px 2px rgba(0,0,0,.1); display: block; text-align: center; margin: 0 auto; margin-bottom: 0.5rem; width: 5rem; border-radius: 0.25rem; background: #fff; }
  .alertLoading {text-align: center; color: #666;; line-height: 200%; display: inline-block; width: 100%; padding: 0.25rem; margin-bottom: 0.25rem; display: none;}
  /*#mall-today .swiper-slide { width: auto }*/
  .swiper-pagination-bullet-active { background: #fff!important; }
  .mall_gray { background: #ccc!important; color: #666!important; }
  .left_goods { position: absolute; left: 0.25rem; top: 0.25rem; width: 1.875rem; height: 1.875rem; background: url(../../../static/left_goods.png) 0 0 no-repeat; -webkit-background-size: 100%;}
  .mall-red{color:red;}
</style>



