<template>
  <div>
    <div id="banner">
      <div class="headerSlider" v-if="bannerList.length">
        <swiper :options="swiperOption" ref="mySwiperA" class="swiper-wrapper">
          <!--  -->
          <swiper-slide class="banner" v-for="item in bannerList">
            <img :src="item.image"/>
          </swiper-slide>
          <!-- ... -->
          <!-- slot -->
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
    </div>

  </div>
</template>
<script>
  import Vue from 'vue'
  import VueAwesomeSwiper from 'vue-awesome-swiper';
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import {getHomePageList} from '../../api';

  Vue.use(VueAwesomeSwiper);
  ////  Vue.use(VueRouter);
  //  const router = new VueRouter({
  //    routes: [
  //      { path: '/my', component: My },
  //      { path: '/download', component:Download },
  //      { path: '/mall', component:Mall }
  //    ]
  //  });
  //
  //  new Vue({ router:router }).$mount('#footer');
  export default{
    data() {
      return {
        bannerList: [],
        swiperOption: {
          // Swiper
          // NotNextTick is a component's own property, and if notNextTick is set to true, the component will not instantiate the swiper through NextTick, which means you can get the swiper object the first time (if you need to use the get swiper object to do what Things, then this property must be true)
          // notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
          notNextTick: true,
          autoplay: 3000,
          direction: 'horizontal',
          grabCursor: true,
          setWrapperSize: true,
          autoHeight: true,
          pagination: '.swiper-pagination',
          paginationClickable: true,
          mousewheelControl: true,
          observeParents: true,
          // if you need use plugins in the swiper, you can config in here like this
          // debugger
          debugger: true,
          // swiper callbacks
          // swiperswiper
          // more Swiper config ...
          // ...
        }
      }
    },
    created(){
      this.getBanner();
    },
    mounted(){
    },
    methods: {
      getBanner(){
        getHomePageList().then((res) => {
          if (res.status == 200) {
            this.bannerList = res.data.data.banner;
          }
        })
      }
    },
    components: {
      swiper,
      swiperSlide
    }
  }
</script>
<style scoped>
  .headerSlider {
    width: 100%;
    height: 9rem;
  }

  .headerSlider .swiper-wrapper {
    width: 100%;
    height: 100%;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
  }
</style>



