
import Mall from '../components/mall/mall.vue';
import Home from '../view/home.vue';
import My from '../components/my.vue';
import Download from '../components/download.vue';

let routers = [
    {
      path: '/',
      component: Home
    },
    {
      path: '/home',
      component: Home
    },
    {
      path: '/my',
      component: My
    },
    {
      path: '/download',
      component: Download
    },
    {
      path: '/mall',
      component: Mall
    }
  ]

export default  routers;



